html,
body {
  font-family: 'Outfit', serif;
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: black;
  overflow-x: hidden;
  /* Prevents horizontal scrolling */
}

.app {
  background-color: black;
  min-height: 100vh;
  /* Ensure the .app div fills at least the viewport height */
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.welcome-screen {
  /* justify-content: center !important; */
  /* align-items: center !important; */
  text-align: center;
}

.content-screen {
  text-align: start;
}

.name-title {
  font-family: 'Playfair Display', serif;
  color: white;
  font-size: 44px;
  font-weight: 700;
}

.welcome-screen .name-title {
  font-size: 50px;
}

.welcome-screen .name-sub-title {
  margin-top: 10px;
  font-weight: 300;
}

.name-sub-title {
  color: rgb(92, 92, 92);
  font-size: 18px;
}

.desc-content {
  color: white;
  font-weight: 200;
  font-size: 22px;
}

.buttons-container {
  display: flex;
  justify-content: space-between;
  margin-top: 130px;
}

.option-button {
  display: flex;
  align-items: center;
  transition: color 0.4s ease;
  color: white;
  /* Adds transition for opacity */
  cursor: pointer;
  /* Changes cursor to pointer */
}

.clickable {
  transition: color 0.4s ease;
}

.clickable:hover {
  color: rgb(96, 155, 228);
}

.option-button:hover {
  color: rgb(92, 92, 92);
}

.option-button-icon {
  padding-right: 9px;
}

.option-button-text {
  font-size: 18px;
}

.screen {
  max-width: 70vw;
  min-width: 70vw;
  /* max-height: 85vh; */
  padding-top: 70px;
  padding-bottom: 70px;
}

.footer {
  padding-bottom: 30px;
  padding-top: 30px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: black;
}

.footer-content {
  font-weight: 300;
  font-size: 14px;
  text-align: center;
  color: rgb(92, 92, 92);
  transition: color 0.4s ease;
}

.footer-content:hover {
  color: white;
}

/* Avatar.css */
.avatar {
  border-radius: 50%;
  /* Makes it circular */
  overflow: hidden;
  /* Ensures the image fits inside the circle */
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ccc;
  width: 80px;
  height: 80px;
  /* Fallback color */
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* Ensures the image covers the entire area */

}

.spacer {
  height: 15px;
}


