/* CSS for fade-in and fade-out effects */
.fade-in-welcome-screen {
  opacity: 0;
  animation: fadeInWelcomeScreen 1.5s forwards;
}

.fade-out-welcome-screen {
  opacity: 1;
  animation: fadeOutWelcomeScreen 1.2s forwards;
}

@keyframes fadeInWelcomeScreen {
  to {
    opacity: 1;
  }
}

@keyframes fadeOutWelcomeScreen {
  to {
    opacity: 0;
  }
}

.fade-in-content-screen {
  opacity: 1;
  transition: opacity 0.7s ease-in;
}

.fade-out-content-screen {
  opacity: 0;
  transition: opacity 0.7s ease-out;
}

