/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  /* .app {
    align-items: center;
  } */

  .app .welcome-start {
    align-items: start;
  }

  .name-title {
    font-size: 30px;
    /* Reduce font size for smaller screens */
  }

  .welcome-screen .name-title {
    font-size: 40px;
    /* Adjust welcome screen title */
  }

  .name-sub-title,
  .option-button-text {
    font-size: 16px;
    /* Smaller subtitle */
  }

  .option-button-icon {
    font-size: 13px;
  }


  .desc-content {
    font-size: 18px;
    /* Smaller description */
  }

  .buttons-container {
    flex-direction: column;
    /* Stack buttons vertically */
    align-items: start;
    /* Center the buttons */
    margin-top: 55px;
    /* Adjust margin for smaller screens */
  }

  .option-button {
    margin-bottom: 15px;
    /* Space between buttons */
  }

  .screen {
    top: 0;
    min-width: 85vw;
    max-width: 85vw;
    /* More width on mobile */
    padding-top: 50px;
    /* Adjust padding */
    padding-bottom: 50px;
    /* Adjust padding */
  }

  .footer {
    padding-bottom: 10px;
    padding-top: 10px;
  }

  .footer-content {
    font-size: 12px;
    /* Smaller footer text */
  }

  .negative-space-for-mobile {
    height: 40px;
  }

  .spacer {
    height: 13px;
  }

  .avatar {
    width: 75px;
    height: 75px;
  }
}